ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.block-title {
  border-radius: 0 !important;
}

.slider-opt04__layout01 .media {
  height: 380px !important;
}

.col-third {
  width: 33%;
}

.col-fourth {
  width: 25%;
}

ul.products-list {
  padding: 0;
  display: flex;
  flex-flow: row wrap;
}

.banner-contain {
  height: auto !important;
}

a.link-to-product img {
  object-fit: cover;
  object-position: center;
}

.nav-menu {
  display: flex;
  flex-flow: row wrap;
  padding: 10px 0;
}

.nav-menu .menu-item {
  margin-right: 15px;
}

.nav-menu .menu-item:hover a {
  color: orange;
}

.nav-menu .menu-item a {
  color: #222222; 
}

.none-box-shadow .vertical-category-block > .wrap-menu {
  display: none;
}

.delete-icon {
  position: absolute;
  top: 5px;
  right: 20px;
  cursor: pointer;
  font-size: 16px;
}

.single-product {
  padding: 30px 0;
}

.sumary-product .product-attribute .category {
  margin: 0 !important;
}

.sumary-product .media .slider-nav li img {
  object-fit: cover;
}

.media .biolife-carousel > li {
  display: inline-block !important;
}

.nav-menu .menu-item {
  position: relative;
}

.nav-menu .menu-item > button {
  background-color: transparent;
  color: initial;
}

.nav-menu .menu-item:hover .sub-menu {
  display: block;
}

.nav-menu .sub-menu {
  min-width: 200px;
  display: none;
  position: absolute;
  left: 0;
  background-color: white;
  box-shadow: 0px 0px 0px 1px rgba(0, 0, 0, 0.1);
  z-index: 11;
}

.nav-menu .sub-menu ul {
  padding: 10px 0;
  width: 100%;
}

.nav-menu .sub-menu li > a {
  padding: 10px 20px;
}
